input {
    width: 80%;
    display: flex;
    margin: -30px auto;
    background-color: rgba(255, 255, 255, 0.537);
    border: none;
    font-size: 20px;
    padding: 10px;
    box-sizing: border-box;
}

input::placeholder {
    color: #999;
}

input:focus {
    outline-color:  #013;
}

